import React, { useState } from "react";
import LocationAndCategory from "./tabs/LocationAndCategory";
import TopicContent from "./tabs/TopicContent";
import Media from "./tabs/Media";
import Preview from "./tabs/Preview";
import Header from "../../semantics/header";
import Footer from "../../semantics/footer";
import checkMark from "../../assets/images/check-mark.png";

import "./style.scss";

class TopicSuggestion extends React.Component {
  state = {
    categories: [],
    page: 1,
    imagesToPreview: [],
    imagesToUpload: [],
    locationAndCategory: {
      form: {
        categories: [],
        address: "",
        lat: "44.55",
        lng: "45.55"
      }
    },
    topicContent: {
      form: {
        title: "",
        description: "",
        amount: ""
      }
    },
    review: {},
    checkMarkOne: false,
    checkMarkTwo: false,
    checkMarkThree: false,
    checkMarkFour: false,
    submittedOne: false,
    submittedTwo: false,
    submittedThree: false,
    submittedFour: false,
    typed: false,
    categoryId: null
  };

  componentDidMount() {
    this.getCategories();
  }

  firstPageChange = e => {
    const {
      locationAndCategory: { form }
    } = this.state;

    form[e.target.name] = e.target.value;
    this.setState({ form });
  };

  secondPageChange = e => {
    const {
      topicContent: { form }
    } = this.state;

    form[e.target.name] = e.target.value;
    this.setState({ form });
  };

  moveNextPage = pageNum => {
    const {
      submittedOne,
      submittedTwo,
      submittedThree,
      submittedFour,
      page
    } = this.state;

    console.log("movw next page");

    const {
      locationAndCategory: {
        form: { address }
      },
      topicContent: {
        form: { title, description, amount }
      },
      categoryId
    } = this.state;

    console.log(page);
    if (page === 1) {
      this.setState({
        submittedOne: true
      });

      console.log(submittedOne, address, categoryId);
      if (submittedOne && address && categoryId) {
        this.setState({
          page: 2,
          checkMarkOne: true
        });
      }
    }

    // if (page === 2) {
    //   this.setState({
    //     submittedTwo: true
    //   });

    //   if (submittedTwo && title && description.length >= 500 && amount) {
    //     this.setState({
    //       page: 3,
    //       checkMarkOne: true,
    //       checkMarkTwo: true
    //     });
    //   }
    // }
    // // topic content

    // // media
    // if (page === 3) {
    //   this.setState({
    //     submittedThree: true
    //   });

    //   if (submittedThree) {
    //     this.setState({
    //       page: 4,
    //       checkMarkOne: true,
    //       checkMarkTwo: true,
    //       checkMarkThree: true
    //     });
    //   }
    // }
  };

  publishHandler = async e => {
    const { locationAndCategory, topicContent, media } = this.state;
    let token = localStorage.getItem("authToken");
    const formData = new FormData();

    formData.append("address", locationAndCategory.form.address);
    formData.append("amount", topicContent.form.amount);
    formData.append("description", topicContent.form.description);
    formData.append("title", topicContent.form.title);
    formData.append("categoryId", this.state.categoryId);

    for (let i = 0; i < this.state.imagesToUpload.length; i++) {
      formData.append("file", this.state.imagesToUpload[i]);
    }
    // formData.append("lat", locationAndCategory.form.lat);
    // formData.append("lng", locationAndCategory.form.lng);

    // console.log(this.state.categoryId, "categoryId");
    // console.log(locationAndCategory.form.location, "address");
    // console.log(locationAndCategory.form.lat, "lat");
    // console.log(locationAndCategory.form.lng, "lng");
    // console.log(topicContent.form.title, "title");
    // console.log(topicContent.form.description, "description");
    // console.log(topicContent.form.amount, "amount");
    // console.log(media.form.file, "file");

    // console.log(token, "authtoken");
    try {
      if (token) {
        const res = await fetch("https://2.evollai.com/api/topic", {
          method: "POST",
          formData,
          headers: {
            "Content-Type": "application/json",
            "Accept-Language": "*",
            language: 1,
            Authorization: `Bearer ${token}`
          }
        });
        const data = await res.json();
      } else {
        // console.log("no token");
      }
    } catch (error) {
      // console.log(error);
    }
  };

  getCategories = async () => {
    const {
      locationAndCategory: {
        form: { categories }
      }
    } = this.state;
    try {
      const response = await fetch("https://2.evollai.com/api/topic/category", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "Accept-Language": "*"
        }
      });
      const result = await response.json();
      console.log(result, "categories");
      if (result) {
        this.setState({
          categories: result.data
        });
      }
    } catch (error) {
      console.log(error);
    }
  };

  sendData = (imagesToPreview, imagesToUpload) => {
    console.log(imagesToUpload);

    this.setState({ imagesToPreview, imagesToUpload });
  };

  onCategoryChange = e => {
    this.setState({
      categoryId: e.target.value
    });
  };

  render() {
    console.log(this.state.imagesToPreview, "image file");
    console.log(this.state.imagesToUpload, "image file");

    const {
      form: { address, categories, categoryId }
    } = this.state.locationAndCategory;
    const {
      form: { title, description, amount }
    } = this.state.locationAndCategory;

    const {
      checkMarkOne,
      checkMarkTwo,
      checkMarkThree,
      checkMarkFour,
      page,
      submittedOne,
      submittedTwo,
      submittedThree,
      submittedFour
    } = this.state;
    return (
      <>
        <Header />
        <div className="suggest-topic">
          <div className="tabs">
            <ul>
              <li>
                Category and <br />
                <span>location</span>
                {checkMarkOne && <img src={checkMark} alt="check-mark" />}
              </li>
              <li>
                Topic content
                {checkMarkOne && checkMarkTwo && (
                  <img src={checkMark} alt="check-mark" />
                )}
              </li>
              <li>
                Media
                {checkMarkOne && checkMarkTwo && checkMarkThree && (
                  <img src={checkMark} alt="check-mark" />
                )}
              </li>
              <li>Preview</li>
            </ul>
          </div>
          <div className="tab-content">
            {page === 1 ? (
              <LocationAndCategory
                moveNextPage={() => this.moveNextPage(1)}
                onChange={this.firstPageChange}
                onCategoryChange={this.onCategoryChange}
                address={address}
                categories={this.state.categories}
                submitted={submittedOne}
                categoryId={this.state.categoryId}
              />
            ) : page === 2 ? (
              <TopicContent
                onChange={this.secondPageChange}
                moveNextPage={() => this.moveNextPage(2)}
                title={title}
                description={description}
                amount={amount}
                submitted={submittedTwo}
                typed={this.state.typed}
              />
            ) : page === 3 ? (
              <Media
                sendData={this.sendData}
                moveNextPage={() => this.moveNextPage(3)}
                submitted={submittedThree}
              />
            ) : (
              <Preview
                images={this.state.imagesToPreview}
                publish={this.publishHandler}
                submitted={submittedFour}
              />
            )}
          </div>
        </div>
        <Footer />
      </>
    );
  }
}

export default TopicSuggestion;
